body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #F7FFF7;
  background-color: #555555;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 15px;
  line-height: 1.5;
}

h1{
  font-size: 4vw;
}

h2{
  font-size: 2.5vw;
  margin: 0;
}

h3 {
  font-size: 1.5vw;
  margin: 0;
}

p, li, a{
  font-size: 1vw
}


/* Global styles------------------------------------*/
.btn{
  color: #000000;
  background-color: #ffffff;
  border: none;
  border-radius: 2px;
  text-align: center;
  text-decoration: none;
  padding: 10px 15px;
  cursor: pointer;

  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
}

.btn:hover{
  background-color: #4596ff;
  color: #ffffff;
}

/* Header styles------------------------------------*/
header {
  background: #343434;
  color: #F7FFF7;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: 25px;
}

header img{
  margin: 40px 0 0 20px;
  float: right;
  height: 400px;
  border: 1px solid #2F3061;
  border-radius: 4px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-wrap {
  max-width: 800px;
  width: 85%;
  margin: 0 auto;
  padding: 60px 0;
}

/* Footer styles------------------------------------*/
footer{
  background-color: #343434;
  padding: 15px 0 15px 25px
}

footer p{
  margin: 0;
}

footer a{
  color: #ffffff;
}

footer a:hover{
  color: #4596ff;
}

  /* Content Styles----------------------------------*/
  .content {
      background: #2F3061;
      margin-bottom: 25px;
      padding: 25px;
  }

  .content-head{
      margin: 35px auto;
      text-align: center;
  }
  